import { RouteConfig } from "vue-router";

const STLoginRoute: Array<RouteConfig> = [
  {
    path: "/student",
    component: () => import("@/views/ap/student/login/Index.vue")
  },
  {
    path: "/student/index",
    component: () => import("@/views/ap/student/login/Index.vue")
  },
  {
    path: "/student/login",
    component: () => import("@/views/ap/student/login/Index.vue")
  },
  {
    path: "/student/login/password_forget",
    component: () => import("@/views/ap/student/login/PasswordForget.vue")
  },
  {
    path: "/student/login/password_reset",
    component: () => import("@/views/ap/student/login/PasswordReset.vue")
  }
];

export { STLoginRoute };